import './App.css';
import QuestionCard from './components/QuestionCard';
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';
import { MainApp } from './components/PageElements/MainApp';
import { theme } from './data/AppTheme'
import { createGlobalStyle, ThemeProvider } from 'styled-components';

const GlobalStyle = createGlobalStyle`
  body{
    background: ${({ theme }) => theme.surface1};
    color: ${({ theme }) => theme.text1};
    font-size: clamp(1rem, 8vw, 1.3rem);
    > * {
      transition: 0.25s ease all;
    }
  }
  h1{
    font-size: clamp(1.2rem, 8vw, 2.8rem);
  }
`
function App() {

  return (
    <Router>
      <Routes>
          <Route exact path='/ec-general-culture-quiz'
          element = {
              <ThemeProvider theme={theme}>
                <GlobalStyle />
                <MainApp className="app">
              <meta charSet="utf-8" />
              <title>Examen Cultura General</title>
              <link rel="icon" href="/public/favicon.ico"/>
                <QuestionCard />
                <p style={{ fontSize: '.7rem', textAlign: 'center' }}>made with  by miglcesp01  {new Date().getFullYear()}</p>
                </MainApp>
              </ThemeProvider>
          }>
          </Route>
        <Route exact path='/'
        element = {
          <div>
            <meta charSet="utf-8" />
            <title>Miguel Licea</title>
            <link rel="icon" href="/public/miguel-licea.ico"/>
            <h1>Hello</h1>
          </div>
          }>
        </Route>
      </Routes>
    </Router>
  );
}

export default App;
