import React from 'react'

const ShowMessage = ({ avg }) => {
   let message = {
      text: '',
      emoji: ''
   }

   if (avg >= 80) {
      message.text = 'Excelente trabajo!';
      message.emoji = '';
   }
   else if (avg >= 70) {
      message.text = 'Bien hecho!';
      message.emoji = ''
   }
   else {
      message.text = 'Siga practicando';
      message.emoji = ''
   }

   return (
      <>
         <h1 className='emoji'>{message.emoji}</h1>
         <h1>{message.text}</h1>
      </>
   )
}

export default ShowMessage
